import React, { Component } from 'react';
import './App.scss';

import HomepageHeader from '../HomepageHeader/HomepageHeader';
import CurrentMatches from '../CurrentMatches/CurrentMatches';
import Footer         from '../Footer/Footer';

const App = (props) =>
  <div className="App">
    <HomepageHeader />
    <CurrentMatches {...props}/>
    <Footer />
  </div>

export default App;



